import { atom } from "jotai";
import { atomWithStorage } from "jotai/utils";
import slugify from "slugify";
import currentLessonAtom from "./currentLessonAtom";

/* Atom para salvar as respostas que o usurio submeter nos pr-testes.*/
export const preTesteAnswersAtom = atomWithStorage(
  "respostasPreTestePorLicao",
  {}
);

/* Tipagem para os argumentos da funo Write de editarRespostasAtom */
export type EditAnswersAction = {
  lessonTitle: string;
  resposta: string;
};

/* Atom para salvar a resposta do usurio no localStorage.
A ideia  que a resposta s seja checada depois do usurio assistir  lio.
Como a inteno  que o lessonState do usurio tambm fique salvo em forma de cookie (j que o site  esttico e no h backend), ento o localStorage serve aqui simultaneamente como global state e database */
export const editPreTesteAnswerAtom = atom(
  null,
  (get: any, set: any, { lessonTitle, resposta }: EditAnswersAction) => {
    let respostas = {
      ...get(preTesteAnswersAtom),
      ...{ [slugify(lessonTitle, { lower: true })]: resposta },
    };
    set(preTesteAnswersAtom, respostas);
  }
);

export const currentLessonPreTesteAnswerAtom = atom(
  // @ts-ignore
  (get) => get(preTesteAnswersAtom)[get(currentLessonAtom)]
);
